.login{
    width: 100%;
    height: 100%;
    background-image: url('../../assets/images/bg.jpg');
    background-size:100% 100%;
    .login-header{
        display: flex;
        align-items: center;
        height: 80px;
        background-color: rgba(21,20,13,0.5);
        img{
            width: 40px;
            height: 40px;
            margin: 0 10px 0 50px;
        }
        h1{
            font-size: 20px;
            color: #ffffff;
        }
    }
    .login-content{
        width: 300px;
        height: 250px;
        margin: 50px auto;
        background-color: #ffffff;
        padding: 20px 30px;
        h2{
            font-size: 20px;
            text-align: center;
            font-weight: bold;
            margin-bottom: 10px;
        }
        .login-form{
            .login-form-button{
                width: 100%;
            }
        }
    }
}
@primary-color: #1DA57A;